<template>
  <el-menu
          :default-active="$route.path"
          class="el-menu-vertical-demo aSider"
          background-color="transparent"
          text-color="#fff"
          active-text-color="#ffd04b"
          router
  >
    <el-submenu v-for="(item,index) in navPath" v-show="tree.indexOf(item.power) >-1" v-cloak :index="item.index" :key="index" style="text-align: left;">
      <template slot="title">
        <span><svg-icon :icon-class="item.icon" class="asiderClass"></svg-icon></span>
        <span style="font-size: 20px;vertical-align: top;">{{item.name}}</span>
      </template>
      <el-menu-item-group>
        <!--<template slot="title">分组一</template>-->
        <el-menu-item v-for="(temp, index) in item.nextNav" :key="index" v-show="tree.indexOf(temp.power)>-1" :index="temp.index">
          <!--<i :class="temp.icon"></i>-->
          <svg-icon :icon-class="temp.icon" class="twoAsiderClass"></svg-icon>
          <span style="font-size: 16px;">{{temp.name}}</span>
          <el-badge style="vertical-align: top;margin-left: 10px;" v-show="temp.name === '待处理' && bubbleNum !== '0' " class="mark" :value="bubbleNum" />
        </el-menu-item>
      </el-menu-item-group>
    </el-submenu>
  </el-menu>
</template>
<script>
import store from '@/store/index'
export default {
  name: 'aSider',
  store,
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      activeNav: '/reference/theme',
      tree: [],
      navPath: [
        {
          name: '参 考',
          index: '1',
          power: 'a1',
          icon: 'reference',
          nextNav: [
            {
              name: '服务主题',
              index: '/reference/theme',
              power: 'a1_b1',
              icon: 'referenceTheme'
            },
            {
              name: '服务事项',
              index: '/reference/matter',
              power: 'a1_b2',
              icon: 'referenceMatter'
            },
            {
              name: '服务要素',
              index: '/reference/element',
              power: 'a1_b3',
              icon: 'referenceElement'
            }
          ]
        },
        {
          name: '呼 叫',
          index: '2',
          power: 'a2',
          icon: 'call',
          nextNav: [
            {
              name: '呼号',
              index: '/call',
              power: 'a2_b1',
              icon: 'callNum'
            }
          ]
        },
        {
          name: '任 务',
          index: '3',
          power: 'a3',
          icon: 'task',
          nextNav: [
            {
              name: '待处理',
              index: '/deal',
              power: 'a3_b1',
              icon: 'deal'
            }
          ]
        },
        {
          name: '数 据',
          index: '4',
          power: 'a4',
          icon: 'data',
          nextNav: [
            {
              name: '数据分析',
              index: '/dataAnalysis',
              power: 'a4_b1',
              icon: 'dataAnalysis'
            }
          ]
        },
        {
          name: '管理要点库',
          index: '5',
          power: 'a5',
          icon: 'elementLibrary',
          nextNav: [
            {
              name: '管理主题',
              index: '/Administration/ATheme',
              power: 'a5_b1',
              icon: 'ATheme'
            },
            {
              name: '管理事项',
              index: '/Administration/AMatter',
              power: 'a5_b2',
              icon: 'AMatter'
            },
            {
              name: '管理要素',
              index: '/Administration/AElement',
              power: 'a5_b3',
              icon: 'AElement'
            }
          ]
        },
        {
          name: '管理大厅',
          index: '6',
          power: 'a6',
          icon: 'hall',
          nextNav: [
            {
              name: '管理地点',
              index: '/Ahall/AAddress',
              power: 'a6_b1',
              icon: 'Address'
            },
            {
              name: '管理区域',
              index: '/Ahall/AWindow',
              power: 'a6_b2',
              icon: 'Window'
            },
            {
              name: '管理短信',
              index: '/Ahall/AMessage',
              power: 'a6_b3',
              icon: 'Message'
            }
          ]
        },
        {
          name: '管理账号',
          index: '7',
          power: 'a7',
          icon: 'Account',
          nextNav: [
            {
              name: '用户管理',
              index: '/Acconut/AUser',
              power: 'a7_b1',
              icon: 'AUser'
            },
            {
              name: '部门管理',
              index: '/Acconut/ADepartment',
              power: 'a7_b2',
              icon: 'ADepartment'
            },
            {
              name: '角色管理',
              index: '/Acconut/ARole',
              power: 'a7_b2',
              icon: 'ARole'
            },
            {
              name: '权限管理',
              index: '/Acconut/AJurisdiction',
              power: 'a7_b3',
              icon: 'AJurisdiction'
            }
          ]
        }
      ]
    }
  },
  computed: {
    bubbleNum: function () {
      return this.$store.state.bubbleNum || 0
    }
  },
  created: function () {
    if (sessionStorage.userInfo) {
      this.tree = JSON.parse(sessionStorage.userInfo).allPermission
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  .asiderClass{
    width: 20px!important;
    height: 20px!important;
  }
  .twoAsiderClass{
    width: 16px!important;
    height: 16px!important;
  }
  .el-menu{
    border-right: none;
  }
</style>
<style lang="scss">
  [v-cloak] {
    display: none;
  }
  .aSider{
    .el-submenu__title i{
      color: #ffffff;
    }
    .el-submenu__title:hover{
      background: #295da1 !important;
    }
    .el-menu-item:focus, .el-menu-item:hover{
      background: #295da1!important;
    }
  }
</style>



