<template>
  <div id="app">
    <el-container class="container">
      <el-aside width="200px" class="webAside">
        <div class="asideWarp">
          <aSider v-if="isAside"></aSider>
        </div>
      </el-aside>
      <el-container>
        <el-header class="webHeader">
          {{ webTitle }}
          <div class="signOutWarp">
            <el-button type="text" class="signOutBtn" @click="outBtnFn">
              <svg-icon icon-class="signOut" class="outIcon"></svg-icon>
            </el-button>
          </div>
          <div class="userCard">
            <div class="userImgWarp">
              <img class="userImg" src="./assets/user1.jpg" alt="">
            </div>
            <div class="userText">
              <p class="userT">部门：<span style="font-weight: lighter;">{{ user.department }}</span></p>
              <p class="userT">姓名：<span style="font-weight: lighter;">{{ user.name }}</span></p>
            </div>
          </div>
        </el-header>
        <el-main class="webMain">
          <keep-alive>
            <router-view v-if="$route.meta.keepAlive"/>
          </keep-alive>
          <router-view v-if="!$route.meta.keepAlive"/>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import lodash from 'lodash'
import aSider from '@/components/aSider'
import store from '@/store/index'
import supportWord from '@/supportWord/index'
var bubbleNumTimer = 1
export default {
  name: 'App',
  store,
  data () {
    return {
      webTitle: supportWord.title,
      isAside: true
    }
  },
  components: { aSider },
  watch: {
    watchAside (val) {
      this.isAside = false
      this.$nextTick(() => {
        this.isAside = true
      })
    }
  },
  computed: {
    watchAside: function () {
      return this.$store.state.isAside || 0
    },
    user: function () {
      return this.$store.state.userInfo || JSON.parse(sessionStorage.userInfo)
    }
  },
  created: function () {
    // 冒泡
    this.intervalBubbleNumFn()
    //
    store.commit('changeBackAside')
  },
  methods: {
    outBtnFn: lodash.debounce(function () {
      this.$confirm('确认是否退出？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        if (sessionStorage.user_id) {
          this.signOutFn({
            user_id: sessionStorage.user_id
          })
        } else {
          this.notifyFn('提示', '用户未登陆')
          this.$router.push('/')
        }
      }).catch(() => {
      })
    }, 600),
    async signOutFn (params) {
      try {
        let res = await this.$api.unsplash.signOut(params)
        console.log(res)
        if (res.errno === '0') {
          sessionStorage.clear()
          this.$router.push('/')
        } else {
          this.notifyFn('提示', '操作错误')
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    intervalBubbleNumFn () {
      clearInterval(bubbleNumTimer)
      let self = this
      if (sessionStorage.user_id) {
        self.getBubbleNumFn({user_id: sessionStorage.user_id})
      }
      bubbleNumTimer = setInterval(function () {
        if (sessionStorage.user_id) {
          self.getBubbleNumFn({user_id: sessionStorage.user_id})
        }
      }, 1000 * 60 * 1)
    },
    async getBubbleNumFn (params) {
      try {
        let res = await this.$api.unsplash.getBubbleNum(params)
        console.log(12, res)
        if (res.errno === '0') {
          sessionStorage.bubbleNum = res.data.total
          store.commit('changeBubbleNum')
        } else {
          this.notifyFn('提示', res.errmsg)
          sessionStorage.bubbleNum = 0
          store.commit('changeBubbleNum')
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
        sessionStorage.bubbleNum = 0
        store.commit('changeBubbleNum')
      }
    },
    notifyFn (title, msg, type = 'warning') {
      this.$notify({
        title: title,
        message: msg,
        type: type,
        dangerouslyUseHTMLString: true,
        duration: 5000,
        position: 'top-right'
      })
    },
    myAlert (txt, title) {
      this.$alert(txt, title, {
        confirmButtonText: '确定',
        callback: action => {
        }
      })
    }
  }
}
</script>

<style lang="scss">
  *{
    margin: 0;
    box-sizing:border-box;
  }
  $hedeaerH: 80px;
  $BH: 40px;
  $asiderW: 200px;
  $contentPadding: 10px;

  html,body{
    min-width: 100vw;
    min-height: 100vh;
    #app {
      font-family: 'Avenir', Helvetica, Arial, sans-serif;
      -webkit-font-smoothing: antialiased;
      -moz-osx-font-smoothing: grayscale;
      text-align: center;
      color: #2c3e50;
      min-width: 100vw;
      min-height: 100vh;
      margin-top: 0;
      position: relative;
      .container{
        width: 100vw;
        height: 100vh;
        .webAside{
          /*<!--border-top: $hedeaerH solid transparent;-->*/
          padding-top: $hedeaerH;
          /*background: rgb(84, 92, 100);*/
          background-color: #3478cd;
          background-image: linear-gradient(90deg, #3478cd 0%, #3478cd 74%);
          overflow: hidden;
          .asideWarp{
            width: 100%;
            height: 100%;
            overflow-y: scroll;
            &::-webkit-scrollbar {display:none}
          }
        }
        .webHeader{
          min-height: $hedeaerH;
          font-size: 40px;
          line-height: $hedeaerH;
          text-align: center;
          color: #ffffff;
          letter-spacing: 4px;
          background: #3478cd;
          position: relative;
          .signOutWarp{
            width: $hedeaerH;
            height: $hedeaerH;
            position: absolute;
            top: 0;right: 0;
            .signOutBtn{
              width: $hedeaerH/2.5;
              height: $hedeaerH/2.5;
              padding: 0;
              position: absolute;
              top: 50%;left: 50%;
              transform: translate(-50%, -50%);
              &:hover{
                color: #ff361e;
              }
              .outIcon{
                width: $hedeaerH/2.5;
                height: $hedeaerH/2.5;
              }
            }
          }
          .userCard{
            width: $asiderW * 1.5;
            height: $hedeaerH;
            position: absolute;
            top: 0;left: -$asiderW;
            /*background: black;*/
            .userImgWarp{
              width: $hedeaerH;
              height: $hedeaerH;
              position: absolute;
              top: 0;left: 0;
              border-radius: 50%;
              .userImg{
                width: $hedeaerH - 16px;
                height: $hedeaerH - 16px;
                display: block;
                position: absolute;
                top: 50%;left: 50%;
                border-radius: 50%;
                transform: translate(-50%, -50%);
                background: #ffffff;
              }
            }
            .userText{
              /*<!--width: $userW;-->*/
              padding-left: $hedeaerH;
              padding-top: 10px;
              padding-bottom: 10px;
              /*<!--padding-right: $userH/2;-->*/
              .userT{
                height: ($hedeaerH)/2 - 10px;
                line-height: ($hedeaerH)/2 - 10px;
                text-align: left;
                font-size: 16px;
                color: #ffffff;
                letter-spacing: 0;
                /*font-weight: bold;*/
              }
            }
          }
        }
        .webMain{
          overflow-y: scroll;
          &::-webkit-scrollbar {display:none}
        }
      }
    }
  }
</style>



