import Vue from 'vue'
import Router from 'vue-router'
// import axios from 'axios'

Vue.use(Router)
export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Sign',
      component: () => import('@/components/sign/sign'),
      meta: {
        name: '登录',
        keepAlive: false
      }
    },
    {
      path: '/mobileSign',
      name: 'mobileSign',
      component: () => import('@/components/sign/mobileSign'),
      meta: {
        name: '移动端登录',
        keepAlive: false
      }
    },
    {
      path: '/fix',
      name: 'fix',
      component: () => import('@/components/sign/fix'),
      meta: {
        name: '修改密码',
        keepAlive: false
      }
    },
    {
      path: '/mobileFix',
      name: 'mobileFix',
      component: () => import('@/components/sign/mobileFix'),
      meta: {
        name: '移动端修改密码',
        keepAlive: false
      }
    },
    {
      path: '/address',
      name: 'address',
      component: () => import('@/components/address/address'),
      meta: {
        name: '地址',
        keepAlive: false
      }
    },
    {
      path: '/mobileAddress',
      name: 'mobileAddress',
      component: () => import('@/components/address/mobileAddress'),
      meta: {
        name: '移动端地址',
        keepAlive: false
      }
    },
    {
      path: '/reference/theme',
      name: 'ServiceTheme',
      component: () => import('@/components/reference/ServiceTheme'),
      meta: {
        name: '服务主题',
        keepAlive: false
      }
    },
    {
      path: '/reference/matter',
      name: 'ServiceMatter',
      component: () => import('@/components/reference/ServiceMatter'),
      meta: {
        name: '服务事项',
        keepAlive: false
      }
    },
    {
      path: '/reference/element',
      name: 'ServiceElement',
      component: () => import('@/components/reference/ServiceElement'),
      meta: {
        name: '服务要素',
        // keepAlive: true
        keepAlive: false
      }
    },
    {
      path: '/call',
      name: 'Call',
      component: () => import('@/components/call/call'),
      meta: {
        name: '呼号',
        keepAlive: true
      }
    },
    {
      path: '/mobileCall',
      name: 'mobileCall',
      component: () => import('@/components/call/mobileCall'),
      meta: {
        name: '移动端呼号',
        keepAlive: true
      }
    },
    {
      path: '/deal',
      name: 'Deal',
      component: () => import('@/components/deal/deal'),
      meta: {
        name: '待处理',
        keepAlive: false
      }
    },
    {
      path: '/dataAnalysis',
      name: 'dataAnalysis',
      component: () => import('@/components/data/dataAnalysis'),
      meta: {
        name: '数据分析',
        keepAlive: false
      }
    },
    {
      path: '/dataAnalysis/matterDetails',
      name: 'matterDetails',
      component: () => import('@/components/data/matterDetails'),
      meta: {
        name: '事项详情',
        keepAlive: false
      }
    },
    {
      path: '/tabData',
      name: 'tabData',
      component: () => import('@/components/data/tabData'),
      meta: {
        name: '标签数据',
        keepAlive: false
      }
    },
    {
      path: '/Administration/ATheme',
      name: 'ATheme',
      component: () => import('@/components/Administration/theme'),
      meta: {
        name: '管理主题',
        keepAlive: true
      }
    },
    {
      path: '/Administration/AMatter',
      name: 'AMatter',
      component: () => import('@/components/Administration/matter'),
      meta: {
        name: '管理事项',
        keepAlive: true
      }
    },
    {
      path: '/Administration/AElement',
      name: 'AElement',
      component: () => import('@/components/Administration/element'),
      meta: {
        name: '管理要素',
        keepAlive: true
      }
    },

    {
      path: '/Ahall/AAddress',
      name: 'AAddress',
      component: () => import('@/components/hall/address'),
      meta: {
        name: '地址管理',
        keepAlive: true
      }
    },
    {
      path: '/Ahall/AWindow',
      name: 'AWindow',
      component: () => import('@/components/hall/window'),
      meta: {
        name: '窗口管理',
        keepAlive: true
      }
    },
    {
      path: '/Ahall/AMessage',
      name: 'AMessage',
      component: () => import('@/components/hall/shortMsg'),
      meta: {
        name: '窗口管理',
        keepAlive: true
      }
    },

    {
      path: '/Acconut/AJurisdiction',
      name: 'AJurisdiction',
      component: () => import('@/components/account/jurisdiction'),
      meta: {
        name: '权限管理',
        keepAlive: true
      }
    },
    {
      path: '/Acconut/ARole',
      name: 'ARole',
      component: () => import('@/components/account/role'),
      meta: {
        name: '角色管理',
        keepAlive: true
      }
    },
    {
      path: '/Acconut/ADepartment',
      name: 'ADepartment',
      component: () => import('@/components/account/department'),
      meta: {
        name: '部门管理',
        keepAlive: true
      }
    },
    {
      path: '/Acconut/AUser',
      name: 'AUser',
      component: () => import('@/components/account/user'),
      meta: {
        name: '用户管理',
        keepAlive: true
      }
    },
    {
      path: '*',
      component: () => import('@/components/errPage/404'),
      meta: {title: '页面走丢了'}
    }
  ]
})



